import { createSessionStorage } from "./session/session";

export async function checkAuthentication(request: any, context: any) {
  const authCookie = createSessionStorage(context.env).getSession(
    context.request.headers.get("Cookie")
  );
  console.log(authCookie, "authCookie");
  if (authCookie) {
    try {
      const parsedCookieData = authCookie;
      return parsedCookieData;
    } catch (error) {
      console.error("Error parsing cookie data:", error);
    }
  }
  return false;
}
