// utils/authenticateAndFetchProfile.ts
import { AuthCookie } from "~/utils/auth/auth"; // Adjust path as needed
import { UserService } from "../../../api/services/user_service"; // Adjust path as needed

async function authenticateAndFetchProfile(request: Request, env: any) {
  const cookie = AuthCookie(env);
  
  console.table(cookie)
  const cookieString = request.headers.get("Cookie");

  const cookieData = await cookie.parse(cookieString);

  
  if (!cookieData) {
    throw new Error("Authentication failed");
  }

  let parsedCookieData;
  try {
    parsedCookieData = JSON.parse(cookieData);
  } catch (error) {
    console.error("Error parsing cookie data:", error);
    throw new Error("Invalid cookie data");
  }

  if (!parsedCookieData.accessToken || !parsedCookieData.refreshToken) {
    throw new Error("Invalid token data");
  }

  UserService.setDefaultHeaders(
    `my_token=${parsedCookieData.accessToken};myRefresh_token=${parsedCookieData.refreshToken}`
  );

  try {
    const user = await UserService.getUserProfile();
    return user;
  } catch (error) {
    console.error("Error fetching user profile:", error);
    throw new Error("Error fetching user profile");
  }
}

export default authenticateAndFetchProfile;
