import { createWorkersKVSessionStorage, logDevReady } from "@remix-run/cloudflare";
import { createPagesFunctionHandler } from "@remix-run/cloudflare-pages";
import { createSessionStorage } from "./app/utils/session/session";
import * as build from "@remix-run/dev/server-build";
import { EnvWithKV } from "api/schemas/kv";

if (process.env.NODE_ENV === "development") {
  logDevReady(build);
}


export const onRequest: PagesFunction<EnvWithKV> = async (context) => {

  const { commitSession, getSession, destroySession } = createWorkersKVSessionStorage(context.env);

  console.log(context, "context1") 
  
  let session = await getSession(
    context.request.headers.get("Cookie")
  );


  let handleRequest = createPagesFunctionHandler({
    build,
    mode: build.mode,
    getLoadContext(context) {
      return { ...context.env, session };
    },
  });

  let response = await handleRequest(context);

  // response.headers.append(
  //   "Set-Cookie",
  //   await commitSession(session)
  // );

  return response;
}
